// Colors
// ---------------------------------

// Default

$color-default-black:               #102654;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-torch-red:                   #ff1553;
$color-chambray:                    #495a7d;
$color-egyptian-blue:               #103aae;
$color-whisper:                     #e8e8e8;
$color-ghost-white:                 #f8f9fc;
$color-torch-red:                   #ed0233;
$color-resolution-blue:             #1c3374;
$color-black-russian:               #030a1a;
$color-tab-current:                 #d10a42;
$color-card-blue:                   rgba(16, 38, 84, 0.89);
$color-text-grey:                   rgba(16, 38, 84, 0.5);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-roboto:                         "Roboto Condensed", "Arial", sans-serif;
$ff-alumni:                         "Alumni Sans", "Arial", sans-serif;


// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-768:                            768px;
$vp-1200:                           1200px;
$vp-1260:                           1260px;
$vp-1302:                           1302px;
